import React from 'react'
import { FAQContext } from '../../../../util/faq'
import './style.css'

const ServiceInfo = ({ context }) => (
    <React.Fragment>
        <div className="service-trigger" onClick={context.actions.toggleService}>
            <h3></h3>
            <i className={context.state.service ? "fas fa-caret-up" : "fas fa-caret-down"} />
        </div>

        <div className={context.state.service ? "service-content" : "service-content hide"}>
            <section><h3><span class="icon is-large has-text-info"><i class="far fa-thumbs-up fa-lg"/></span> </h3><ul><li></li><li></li><li></li><li></li><li></li><li></li><li></li></ul></section>
            <section><h3><span class="icon is-large has-text-info"><i class="far fa-thumbs-up fa-lg"/></span> </h3><ul><li></li><li></li><li></li><li></li><li> </li><li></li></ul></section>
            <section><h3><span class="icon is-large has-text-info"><i class="far fa-thumbs-up fa-lg"/></span> </h3><ul><li></li><li></li><li>OWWA</li><li></li></ul></section>
            <section><h3><span class="icon is-large has-text-info"><i class="far fa-thumbs-up fa-lg"/></span> <strong>(Direct Hiring)</strong></h3><ul><li></li><li></li><li></li><li></li><li></li></ul></section>
        </div>
    </React.Fragment>
)

export default () => (
    <FAQContext>
        { context => <ServiceInfo context={context} /> }
    </FAQContext>
)


