import React from 'react'
import { FAQContext } from '../../../../util/faq'
import './style.css'

const AfterArrival = ({ context }) => (
    <React.Fragment>
        <div className="after-arrival-trigger" onClick={context.actions.toggleAfterArrival}>
            <h3></h3>
            <i className={context.state.after_arrival ? "fas fa-caret-up" : "fas fa-caret-down"} />
        </div>

        <div className={context.state.after_arrival ? "after-arrival-content" : "after-arrival-content hide"}>
            <ol>
                <li><strong>,,</strong></li>
                <li><strong>(),,</strong></li>
                <li><strong></strong></li>
                <li><strong>():</strong><ul><li>,</li></ul></li>
                <li><strong>,:</strong><ul><li>4,4/5,2,4,120</li></ul></li>
                <li><strong>,(): </strong><ul><li>,260</li><li></li></ul></li>
                <li><strong>,(),():</strong><ul><li>,</li><li></li></ul></li>
                <li><strong>5,65:</strong><ul><li>x(2/3</li><li>,</li></ul></li>
                <li><strong></strong><ul><li>:100</li></ul></li>
                <li><strong></strong><ul><li>,,</li><li>.,,.</li><li>,..</li></ul></li>
            </ol>
        </div>
    </React.Fragment>
)
export default () => (
    <FAQContext>
        { context => <AfterArrival context={context} /> }
    </FAQContext>
)


